html,body{height:100%;background-color:#eee}html,body,input,textarea,buttons{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-shadow:1px 1px 1px rgba(0,0,0,0.004)}#container,#root{height:100%}#header{position:fixed;top:0;right:0;left:0;z-index:2;transition:left .2s}#sidedrawer{position:fixed;top:0;bottom:0;width:200px;left:-200px;overflow:auto;z-index:2;background-color:#fff;transition:transform .2s}#content-wrapper{min-height:100%;overflow-x:hidden;margin-left:0;transition:margin-left .2s;box-sizing:border-box;margin-bottom:-100px;padding-bottom:100px}#footer{box-sizing:border-box;height:100px;margin-left:0;transition:margin-left .2s}@media (min-width:768px){#header{left:200px}#sidedrawer{transform:translate(200px)}#content-wrapper{margin-left:200px}#footer{margin-left:200px}body.hide-sidedrawer #header{left:0}body.hide-sidedrawer #sidedrawer{transform:translate(0)}body.hide-sidedrawer #content-wrapper{margin-left:0}body.hide-sidedrawer #footer{margin-left:0}}#sidedrawer.active{transform:translate(200px)}.sidedrawer-toggle{color:#fff;cursor:pointer;font-size:20px;line-height:20px;margin-right:10px}.sidedrawer-toggle:hover{color:#fff;text-decoration:none}#sidedrawer-brand{padding-left:20px}#sidedrawer ul{list-style:none}#sidedrawer>ul{padding-left:0}#sidedrawer>ul>li:first-child{padding-top:15px}#sidedrawer strong{display:block;padding:15px 22px;cursor:pointer}#sidedrawer strong:hover{background-color:#E0E0E0}#sidedrawer strong+ul>li{padding:6px 0}#footer{background-color:#78909C;color:#fff;margin-top:16px}#footer a{color:#fff;text-decoration:underline}.mui-appbar{background-color:#78909C}[class*=" icon-"],[class^=icon-]{font-family:FontAwesome;speak:none;font-style:normal;font-weight:400;font-variant:normal;text-transform:none;line-height:1;-webkit-font-smoothing:antialiased}.icon-menu:before{content:"\f0c9"}.icon-github:before{content:"\f092"}.icon-facebook:before{content:"\f082"}.icon-twitter:before{content:"\f081"}.icon-share:before{content:"\f1e0"}.demo-card-wide.mdl-card{width:100%}.demo-card-wide>.mdl-card__title{background-color:#78909C;color:#fff}.demo-card-wide>.mdl-card__menu{color:#fff}.demo-card-wide>.mdl-card__title>.mdl-card__title-text{line-height:2}#content-wrapper .mdl-grid .mdl-cell{padding:16px 8px 0 8px}.mdl-card-avatar{margin-right:20px}.col-centered{text-align:center;margin:0 auto}


